.tovch button {
  width: 200px;
  height: 70px;
  border-radius: 15px;
  background-image: linear-gradient(
    to right top,
    #051937,
    #004d7a,
    #008793,
    #00bf72,
    #a8eb12
  );
  font-size: 25px;
}
.its {
  display: flex;
  margin-top: 20px;
  justify-self: center;
  align-items: center;
}

.name {
  background-image: linear-gradient(
    180deg,
    rgb(208, 255, 0),
    rgb(66, 255, 183)
  );
  display: flex;
  justify-content: center;
  font-size: 50px;
  background: -webkit-linear-gradient(#8100fa, #8b008b, #ff00ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.tovch {
  display: flex;
  align-items: center;
  justify-content: center;
}
