.tic-tac {
  width: 100%;
  height: 100%;
  background-color: white;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 10px;
  font-size: 250px;
}
.container {
  background-color: #eaf0f1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  /* aspect-ratio: 1; */
  /* height: 100%;
  height: 280px;
  width: 280px; */
}
.box {
  background-color: #8b78e6;
  width: 30%;
  height: 30%;
  border-radius: 16px;
  font-size: 50%;
  display: flex;
  justify-content: center;
  color: whitesmoke;
}
.box:hover {
  background-color: #f3b431;
  cursor: pointer;
}
