.darah-heseg {
  border: 1px solid black;
  height: 70vh;
  width: 98vw;

  cursor: pointer;
  margin-top: 20px;
  margin-inline: 20px;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 60px;
}
.Hairtsag {
  background-color: purple;
  width: 300px;
  height: 200px;
  border-radius: 30px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Container {
  display: flex;
  justify-content: space-between;
}
.pink {
  background-image: linear-gradient(
    to right,
    rgb(255, 123, 0),
    rgb(218, 255, 9)
  );
}

.orange {
  background-image: linear-gradient(to right, blueviolet, blue);
}
.green {
  background-image: linear-gradient(to right, lightseagreen, yellowgreen);
}
